.postItem {
    display: flex ;
    align-items: flex-end;
    margin: 10px;
}

.avatar img{
    display: flex;
    align-items: flex-end;

    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.postMessage {
    padding: 10px;

    border-radius: 5px 5px 5px 0;
    background-color: rgba(255,255,255,0.3);



    min-width: 150px;
    max-width: 300px;
    word-wrap: break-word;
    font-family: Arial,sans-serif;
}

.angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, rgba(255, 255, 255, 0.3) 0);

}
